import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend } from 'recharts';

const App = () =>
{
  const [ priceData, setPriceData ] = useState( [] );

  useEffect( () =>
  {
    const fetchData = async () =>
    {
      const result = await axios(
        'https://api.coindesk.com/v1/bpi/historical/close.json?start=2022-01-01&end=2022-02-28'
      );
      const data = [];
      for ( let date in result.data.bpi )
      {
        data.push( { date: date, price: result.data.bpi[ date ] } );
      }
      setPriceData( data );
    };
    fetchData();
  }, [] );

  return (
    <div>
      <h1>Bitcoin Price Index in February 2022</h1>
      {priceData.length > 0 && (
        <div style={{ width: '100%', height: 300 }}>
          <LineChart data={priceData}>
            <XAxis dataKey="date" />
            <YAxis />
            <CartesianGrid strokeDasharray="3 3" />
            <Tooltip />
            <Legend />
            <Line type="monotone" dataKey="price" stroke="#8884d8" />
          </LineChart>
        </div>
      )}
    </div>
  );
};

export default App;